.skill-box .skill-ico {
    margin-bottom: 1rem;
    color: #ff533d;
}

.skill-box .ico-circle {
    transition: all 500ms ease;
    height: 40px;
    width: 40px;
    font-size: 1.7rem;
    border-radius: 50%;
    line-height: 1.4;
    box-shadow: 0 0 0 3px #ff533d;
    margin: 0 auto;
    text-align: center;
    display: block;
    touch-action: manipulation;
}

.skill-box .ico-circle:hover {
    transition: all 500ms ease;
    color: #f5f5f5;
    background-color: #ff533d;
    box-shadow: 0 0 0 3px #ff998c;
}